import React from 'react';
import { connect } from 'react-redux';
import * as reduxFunctions from '../../store/actions/index';
import classNames from './Bar.module.css';

const Bar = (props) => {
    return (
        <div className={classNames.container} style={props.sideMenuToggle?{right: '0px'}:{right: '-100%'}}>
            <button onClick={()=>{props.pheadersToggle()}}>
                Toggle placement headers
            </button>
            <button onClick={()=>{props.gridToggle()}}>
                Toggle grid
            </button>
        </div>
    );
};

const mapStatetoProps = state => {
    return{
        sideMenuToggle : state.numpad.sideMenuToggle
    }
}
const mapDispatchtoProps = dispatch => {
    return{
        pheadersToggle: () => dispatch(reduxFunctions.pHeadersToggle()),
        gridToggle: () => dispatch(reduxFunctions.gridToggle()),
    }
}
export default connect(mapStatetoProps, mapDispatchtoProps)(Bar);