import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import {combineReducers, createStore} from 'redux';
import {Provider} from 'react-redux';
import reportWebVitals from './reportWebVitals';

import numbers from './store/reducers/numbers';
import carrys from './store/reducers/carry';
import results from './store/reducers/results';
import multiplication from './store/reducers/multiplication';
import numberPad from './store/reducers/numberPad';
import sidemenu from './store/reducers/sidemenu';

const rootReducer = combineReducers({
  number : numbers,
  carry : carrys,
  result : results,
  multi: multiplication,
  numpad: numberPad,
  menu: sidemenu
})



const store = createStore(rootReducer, window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__())

ReactDOM.render(
  <Provider store={store}>
    <React.StrictMode>
      <App />
    </React.StrictMode>
  </Provider>,
  document.getElementById('root')
);

reportWebVitals();
