const finalResultFn = (carry, n1, n2, n3) =>{
    let resultNumber;
    let resultNum;
    let carryingNum;
    const rNumFn = (resultNumber) =>{
        if(resultNumber.toString().length > 1){
            resultNum = Number(resultNumber.toString().slice(1));
            carryingNum = Number(resultNumber.toString().slice(0,1));
        }else{
            resultNum = resultNumber;
        }
    }
    if(carry){
        resultNumber = n1 + n2 + n3 + carry;
        rNumFn(resultNumber);
    }else{
        resultNumber = n1 + n2 + n3;
        rNumFn(resultNumber);
    }
    return {resultNum,carryingNum};
}

export default finalResultFn;