import React from 'react';
import { connect } from 'react-redux';
import classNames from '../../containers/NumberBoard/Board.module.css';

const Row = (props) => {
    let classes;
    switch (props.color) {
        case 'c1':
            if(props.grid){
                classes = `${classNames.carry} ${classNames.c1} ${classNames.brc}`
            }else{
                classes = `${classNames.carry} ${classNames.c1}`
            }
            break;
        case 'c2':
            if(props.grid){
                classes = `${classNames.carry} ${classNames.c2} ${classNames.brc}`
            }else{
                classes = `${classNames.carry} ${classNames.c2}`
            }
            break;
        case 'c3':
            if(props.grid){
                classes = `${classNames.carry} ${classNames.c3} ${classNames.brc}`
            }else{
                classes = `${classNames.carry} ${classNames.c3}`
            }
            break;
    
        default:
            classes = `${classNames.carry} ${classNames.c1} ${classNames.brc}`
            break;
    }
    return (
        <>
        <div className={classNames.row}>
            <div className={classes} style={props.numbers.hundredThousands.focusAction ? {backgroundColor: '#ffffc0'} : null}>
                <h2>{props.numbers.hundredThousands.value}</h2>
            </div>
            <div className={classes} style={props.numbers.tenThousands.focusAction ? {backgroundColor: '#ffffc0'} : null}>
                <h2>{props.numbers.tenThousands.value}</h2>
            </div>
            <div className={classes} style={props.numbers.thousands.focusAction ? {backgroundColor: '#ffffc0'} : null}>
                <h2>{props.numbers.thousands.value}</h2>
            </div>
            <div className={classes} style={props.numbers.hundreds.focusAction ? {backgroundColor: '#ffffc0'} : null}>
                <h2>{props.numbers.hundreds.value}</h2>
            </div>
            <div className={classes} style={props.numbers.tens.focusAction ? {backgroundColor: '#ffffc0'} : null}>
                <h2>{props.numbers.tens.value}</h2>
            </div>
            <div className={classes}>
                <h2>{props.numbers.ones.value}</h2>
            </div>
        </div>
        </>
    );
};

const mapStatetoProps = state => {
    return{
        grid : state.menu.grid
    }
}

export default connect(mapStatetoProps)(Row);