import * as actionTypes from '../actions/actions';

const initialState = {
    topLine:{
        ones: {focusAction: false, value: null},
        tens: {focusAction: false, value: null},
        hundreds: {focusAction: false, value: null},
        thousands: {focusAction: false, value: null},
        tenThousands: {focusAction: false, value: null},
        hundredThousands: {focusAction: false, value: null}
    },
    middleLine:{
        ones: {focusAction: false, value: null},
        tens: {focusAction: false, value: null},
        hundreds: {focusAction: false, value: null},
        thousands: {focusAction: false, value: null},
        tenThousands: {focusAction: false, value: null},
        hundredThousands: {focusAction: false, value: null}
    },
    bottomLine:{
        ones: {focusAction: false, value: null},
        tens: {focusAction: false, value: null},
        hundreds: {focusAction: false, value: null},
        thousands: {focusAction: false, value: null},
        tenThousands: {focusAction: false, value: null},
        hundredThousands: {focusAction: false, value: null}
    }
};

const carry = (state = initialState, action)=>{
    if(action.type === actionTypes.END){
        return initialState;
    }
    let returnedObject = initialState;
    const stepsFn = (obj) => {
        return {
            ...initialState,
            [obj.line] : {
                ...initialState[obj.line],
                [obj.focusPos] : {
                    focusAction: obj.carryNumb?true:false,
                    value: obj.carryNumb
                }
            }
        }
    }

    if(action.type === actionTypes.COUNTERCARRY){

        switch (action.position) {
            case 0:
                if(action.upperDigit === 1 && action.lowerDigit === 1){
                    returnedObject = initialState;
                }else{
                    returnedObject = stepsFn({
                        line: 'bottomLine',
                        focusPos: 'tens',
                        carryNumb: action.carryNumbers.cL1.cL11
                    });
                }
                return returnedObject;
            case 1:
                const logic1  = action.upperDigit === 2 && action.lowerDigit === 1;
                const logic2 = action.upperDigit === 2 && action.lowerDigit === 2;
                if(logic1 || logic2){
                    returnedObject = initialState;
                }else{
                    returnedObject = stepsFn({
                        line: 'bottomLine',
                        focusPos: 'hundreds',
                        carryNumb: action.carryNumbers.cL1.cL12
                    })
                }
                return returnedObject;
            case 2:
                return initialState
            case 4:
                return stepsFn({
                    line: 'middleLine',
                    focusPos: 'hundreds',
                    carryNumb: action.carryNumbers.cL2.cL21
                })
            case 5:
                if(action.upperDigit === 2 && action.lowerDigit === 2){
                    returnedObject = initialState;
                }else{
                    returnedObject = stepsFn({
                        line: 'middleLine',
                        focusPos: 'thousands',
                        carryNumb: action.carryNumbers.cL2.cL22
                    })
                }
                return returnedObject
            case 6:
                return initialState
            case 8:
                return stepsFn({
                    line: 'topLine',
                    focusPos: 'thousands',
                    carryNumb: action.carryNumbers.cL3.cL31
                })
            case 9:
                return stepsFn({
                    line: 'topLine',
                    focusPos: 'tenThousands',
                    carryNumb: action.carryNumbers.cL3.cL32
                })
            case 10:
                return initialState
        
            default:
                return state
        }

    }
    
    if(action.type === actionTypes.COUNTERPREV){
        const logic11 = action.upperDigit === 1 && action.lowerDigit === 1;
        const logic21 = action.upperDigit === 2 && action.lowerDigit === 1;
        const logic22 = action.upperDigit === 2 && action.lowerDigit === 2;
        const logic31 = action.upperDigit === 3 && action.lowerDigit === 1;
        const logic32 = action.upperDigit === 3 && action.lowerDigit === 2;
        const logic33 = action.upperDigit === 3 && action.lowerDigit === 3;

        let c1, c2, c3, c4, c5, c6;

        c1 = action.resultDigits.carry.cL1.cL11;
        c2 = action.resultDigits.carry.cL1.cL12;
        c3 = action.resultDigits.carry.cL2.cL21;
        c4 = action.resultDigits.carry.cL2.cL22;
        c5 = action.resultDigits.carry.cL3.cL31;
        c6 = action.resultDigits.carry.cL3.cL32;

        switch (action.position){
            case 1:
                return initialState
            case 2:
                if(logic11){
                    returnedObject = initialState;
                }else if(logic33 || logic32 || logic31){
                    returnedObject = stepsFn({
                        line: 'bottomLine',
                        focusPos: 'tens',
                        carryNumb: c1
                    });
                }
                return returnedObject;
            case 3:
                if(logic21 || logic22){
                    returnedObject = initialState;
                }else if(logic33 || logic32 || logic31){
                    returnedObject = stepsFn({
                        line: 'bottomLine',
                        focusPos: 'hundreds',
                        carryNumb: c2
                    })
                }
                return returnedObject;
            case 5:
                return initialState
            case 6:
                return stepsFn({
                    line: 'middleLine',
                    focusPos: 'hundreds',
                    carryNumb: c3
                })
            case 7:
                if(logic22){
                    returnedObject = initialState;
                }else if(logic33){
                    returnedObject = stepsFn({
                        line: 'middleLine',
                        focusPos: 'thousands',
                        carryNumb: c4
                    })
                }
                return returnedObject
            case 9:
                return initialState
            case 10:
                if(logic33){
                    return stepsFn({
                        line: 'topLine',
                        focusPos: 'thousands',
                        carryNumb: c5
                    })
                }else if(logic32){
                    return stepsFn({
                        line: 'middleLine',
                        focusPos: 'hundreds',
                        carryNumb: c3
                    })
                }
                return returnedObject
            case 11:
                if(logic33){
                    return stepsFn({
                        line: 'topLine',
                        focusPos: 'tenThousands',
                        carryNumb: c6
                    })
                }else if(logic32){
                    return stepsFn({
                        line: 'middleLine',
                        focusPos: 'thousands',
                        carryNumb: c4
                    })
                }
                return returnedObject
            default:
                return state
        }
    }else{
        return state
    }

};

export default carry;