import * as actionTypes from '../actions/actions';

const initialState = {
    inputFocusTop:{
        0: false,
        1: false,
        2: true
    },
    inputFocusBottom:{
        0: false,
        1: false,
        2: false
    },
    toggleKeypad: true,
    sideMenuToggle: false
};
const falsyObject = {
    inputFocusTop:{
        0: false,
        1: false,
        2: false
    },
    inputFocusBottom:{
        0: false,
        1: false,
        2: false
    }
}
const numberPad = (state = initialState, action)=>{
    if(action.type === actionTypes.START || action.type === actionTypes.GENERATEDONSTART || action.type === actionTypes.LASTFOCUS){
        return {
            ...state,
            ...falsyObject
        }
    }
    if(action.type === actionTypes.END){
        return {
            ...state,
            ...falsyObject,
            inputFocusTop:{
                ...falsyObject.inputFocusTop,
                2: true
            }
        }
    }
    switch (action.type) {
        case actionTypes.INPUTFOCUS:
            return {
                ...falsyObject,
                [action.position]:{
                    ...falsyObject[action.position],
                    [action.current] : true
                }
            }
        case actionTypes.KEYPRESSMULTI:
            return {
                ...falsyObject,
                inputFocusBottom:{
                    ...falsyObject.inputFocusBottom,
                    2: true
                }
            }
        case actionTypes.KEYPADTOGGLE:
            return{
                ...state,
                toggleKeypad: !state.toggleKeypad
            }
        case actionTypes.SIDEMENUTOGGLE:
            return{
                ...state,
                sideMenuToggle: !state.sideMenuToggle
            }
        default:
            return state
    };
};

export default numberPad;