import React from 'react';
import { connect } from 'react-redux';
import classNames from '../../containers/NumberBoard/Board.module.css';

const Row = (props) => {
    let classes;
    switch (props.color) {
        case 'r1':
            if(props.grid){
                classes = `${classNames.results} ${classNames.r1} ${classNames.brf}`
            }else{
                classes = `${classNames.results} ${classNames.r1}`
            }
            break;
        case 'r2':
            if(props.grid){
                classes = `${classNames.results} ${classNames.r2} ${classNames.brs}`
            }else{
                classes = `${classNames.results} ${classNames.r2}`
            }
            break;
        case 'r3':
            if(props.grid){
                classes = `${classNames.results} ${classNames.r3} ${classNames.brt}`
            }else{
                classes = `${classNames.results} ${classNames.r3}`
            }
            break;
        case 'r4':
            if(props.grid){
                classes = `${classNames.results} ${classNames.r4} ${classNames.brl}`
            }else{
                classes = `${classNames.results} ${classNames.r4}`
            }
            break;
    
        default:
            break;
    }
    let zeroColor = '#D9D9D9';
    if(window.innerHeight < 420){
        zeroColor = 'black'
    }
    return (
        <div className={classNames.row}>
            <div className={classes} style={props.numbers.hundredThousands.focusAction ? {backgroundColor: '#ffffc0'} : props.blank || props.blankH ? {backgroundColor: 'transparent'} : null}>
                {props.fRC ? <span className={classNames.frCarry}>{props.fRC.hundredThousands.carry}</span>:null}
                <h1>{props.numbers.hundredThousands.value}</h1>
            </div>
            <div className={classes} style={props.numbers.tenThousands.focusAction ? {backgroundColor: '#ffffc0'} : props.blank ? {backgroundColor: 'transparent'} : null}>
                {props.fRC ? <span className={classNames.frCarry}>{props.fRC.tenThousands.carry}</span>:null}
                <h1>{props.numbers.tenThousands.value}</h1>
            </div>
            <div className={classes} style={props.numbers.thousands.focusAction ? {backgroundColor: '#ffffc0'} : null}>
                {props.fRC ? <span className={classNames.frCarry}>{props.fRC.thousands.carry}</span>:null}
                <h1>{props.numbers.thousands.value}</h1>
            </div>
            <div className={classes} style={props.numbers.hundreds.focusAction ? {backgroundColor: '#ffffc0'} : null}>
                {props.fRC ? <span className={classNames.frCarry}>{props.fRC.hundreds.carry}</span>:null}
                <h1>{props.numbers.hundreds.value}</h1>
            </div>
            <div className={classes} style={props.numbers.tens.focusAction ? {backgroundColor: '#ffffc0'} : props.zero ? {backgroundColor: 'transparent', color: zeroColor} : null}>
                {props.fRC ? <span className={classNames.frCarry}>{props.fRC.tens.carry}</span>:null}
                <h1>{props.numbers.tens.value}</h1>
            </div>
            <div className={classes} style={props.numbers.ones.focusAction ? {backgroundColor: '#ffffc0'} : props.zero || props.zeroO ? {backgroundColor: 'transparent', color: zeroColor} : null}>
                <h1>{props.numbers.ones.value}</h1>
            </div>
        </div>
    );
};


const mapStatetoProps = state => {
    return{
        grid : state.menu.grid
    }
}

export default connect(mapStatetoProps)(Row);