export const randomNumberGenerateOnStart = (topRow, bottomRow) => {
    return{
        type: actionTypes.GENERATEONSTART,
        topRow: topRow,
        bottomRow: bottomRow
    }
}
export const generatedOnStart = (topRow, bottomRow) => {
    return{
        type: actionTypes.GENERATEDONSTART,
        topRow: topRow,
        bottomRow: bottomRow
    }
}
