let emptySpaceText = 'Nothing';

const texts_2x1 = (t1, t2, b1, cr1, r1, r2, r3) => {

    let c1;

    c1 = cr1 ? cr1 : emptySpaceText;

    return [
        {
            text: `Carries are ${c1}`
        },
        {
            text: `2------------------`
        },
        {
            text: `3------------------`
        }
    ]
}

export default texts_2x1;