const resultNumFn = (prev, p1, p2) => {
    let resultNumber;
    let resultNum;
    let result;
    const rNumFn = (resultNumber) =>{
        if(resultNumber.toString().length > 1){
            resultNum = Number(resultNumber.toString().slice(1));
        }else{
            resultNum = resultNumber;
        }
        return resultNum;
    }
    if(prev){
        resultNumber = (p1 * p2) + prev;
        result = rNumFn(resultNumber);
    }else{
        resultNumber = p1 * p2;
        result = rNumFn(resultNumber);
    }
    return result;
}
export default resultNumFn;