import React from 'react';
import { connect } from 'react-redux';
import classNames from './Keypad.module.css';
import * as reduxFunctions from '../../store/actions/index';

const Keypad = (props) => {
    const getIndexPosition = () => {
        let convertToArray = Object.values(props.iFT) 
        let index = convertToArray.indexOf(true);
        let position = 'topRow';
        if(index === -1){
            convertToArray = Object.values(props.iFB);
            index = convertToArray.indexOf(true);
            position = 'bottomRow'
            // if(index === -1){
            //     index = true
            // }
        }
        return {index, position};
    }
    const onKeyPress = (key) => {
        const indexPosition = getIndexPosition();
        if(indexPosition.index === -1){
            alert('Press the start button below')
        }else{
            if(indexPosition.position === 'bottomRow'){
                props.onPress(key, indexPosition.index, 'bottomRow')
                if(indexPosition.index-1 !== -1){
                    props.inputFocus(indexPosition.index-1, 'inputFocusBottom' )
                }else{
                    props.lastFocus()
                }
            }else{
                props.onPress(key, indexPosition.index, 'topRow')
                props.inputFocus(indexPosition.index-1, 'inputFocusTop')
                if(indexPosition.index-1 === -1 && props.tR[2]){
                    props.inputFocus(2, 'inputFocusBottom' )
                }
            }
        }
    }

    const multiply = () => {
        if(!props.bR[2] || getIndexPosition().position === 'topRow') props.multiply();
    }

    const deleteDigit = () => {
        const indexPosition = getIndexPosition();
        if(indexPosition.position === 'bottomRow'){
            if(indexPosition.index === -1){
                props.deleteDigit(0, 'bottomRow');
                props.inputFocus(1, 'inputFocusBottom' );
            }else{
                props.deleteDigit(indexPosition.index, 'bottomRow');
                if(indexPosition.index+1 !== 3){
                    props.inputFocus(indexPosition.index+1, 'inputFocusBottom');
                }else{
                    props.inputFocus(0, 'inputFocusTop' )
                }
            }
        }else{
            props.deleteDigit(indexPosition.index, 'topRow');
            if(indexPosition.index+1 !== 3){
                props.inputFocus(indexPosition.index+1, 'inputFocusTop')
            }else{
                props.inputFocus(2, 'inputFocusTop')
            }
        }
    }
    return (
        <div className={classNames.container}>
            <div className={classNames.digits}>
                <button className={classNames.digit} onClick={()=>{if(!props.inputDisable){onKeyPress(7)}}}>7</button>
                <button className={classNames.digit} onClick={()=>{if(!props.inputDisable){onKeyPress(8)}}}>8</button>
                <button className={classNames.digit} onClick={()=>{if(!props.inputDisable){onKeyPress(9)}}}>9</button>
                <button className={classNames.digit} onClick={()=>{if(!props.inputDisable){onKeyPress(4)}}}>4</button>
                <button className={classNames.digit} onClick={()=>{if(!props.inputDisable){onKeyPress(5)}}}>5</button>
                <button className={classNames.digit} onClick={()=>{if(!props.inputDisable){onKeyPress(6)}}}>6</button>
                <button className={classNames.digit} onClick={()=>{if(!props.inputDisable){onKeyPress(1)}}}>1</button>
                <button className={classNames.digit} onClick={()=>{if(!props.inputDisable){onKeyPress(2)}}}>2</button>
                <button className={classNames.digit} onClick={()=>{if(!props.inputDisable){onKeyPress(3)}}}>3</button>
                <button className={classNames.digit} onClick={()=>{multiply()}}>
                    <i className="fas fa-times"></i>
                </button>
                <button className={classNames.digit} onClick={()=>{if(!props.inputDisable){onKeyPress(0)}}}>0</button>
                <button className={classNames.digit} onClick={()=>deleteDigit()}>
                    <i className="fas fa-backspace"></i>
                </button>
            </div>
        </div>
    );
};

const mapStatetoProps = state => {
    return{
        tR: state.number.topRow,
        bR: state.number.bottomRow,
        iFT : state.numpad.inputFocusTop,
        iFB : state.numpad.inputFocusBottom,
        inputDisable: state.number.inputDisable
    }
}
const mapDispatchtoProps = dispatch => {
    return{
        onPress: (key, index, changeFn) => dispatch(reduxFunctions.keypress(key, index, changeFn)),
        inputFocus: (c, fn) => dispatch(reduxFunctions.inputFocus(c, fn)),
        multiply: () => dispatch(reduxFunctions.multiply()),
        deleteDigit: (index, position) => dispatch(reduxFunctions.deleteDigit(index, position)),
        lastFocus: () => dispatch(reduxFunctions.lastFocus()),
    }
}

export default connect(mapStatetoProps, mapDispatchtoProps)(Keypad);