const carryNumFn = (prev, p1, p2) => {
    let carryNumber;
    let carryNum;
    const cNumFn = (carryNumber) =>{
        if(carryNumber.toString().length > 1){
            carryNum = Number(carryNumber.toString().slice(0,1))
        }else{
            carryNum = null;
        }
        return carryNum;
    }
    if(prev){
        carryNumber = (p1 * p2) + prev;
        carryNum = cNumFn(carryNumber);
    }else{
        carryNumber = p1 * p2;
        carryNum = cNumFn(carryNumber);
    }
    return carryNum;
}

export default carryNumFn;