import * as actionTypes from '../actions/actions';
import carryNumFn from './Utility/carry';
import resultNumFn from './Utility/result';
import finalResultFn from './Utility/finalResult';

const initialState = {
    answer : {}
};

const multiplication = (state = initialState, action)=>{
    if(action.type === actionTypes.START || action.type === actionTypes.GENERATEDONSTART){
        
        const t1 = Number(action.topRow[2]);
        const t2 = Number(action.topRow[1]);
        const t3 = Number(action.topRow[0]);
        const b1 = Number(action.bottomRow[2]);
        const b2 = Number(action.bottomRow[1]);
        const b3 = Number(action.bottomRow[0]);

        const c1 = carryNumFn(null, t1, b1);
        const c2 = carryNumFn(c1, t2, b1);
        const c3 = carryNumFn(c2, t3, b1);
        const c4 = carryNumFn(null, t1, b2);
        const c5 = carryNumFn(c4, t2, b2);
        const c6 = carryNumFn(c5, t3, b2);
        const c7 = carryNumFn(null, t1, b3);
        const c8 = carryNumFn(c7, t2, b3);
        const c9 = carryNumFn(c8, t3, b3);

        const r1 = resultNumFn(null, t1, b1);
        const r2 = resultNumFn(c1, t2, b1);
        const r3 = resultNumFn(c2, t3, b1);
        const r4 = c3;
        const r5 = resultNumFn(null, t1, b2);
        const r6 = resultNumFn(c4, t2, b2);
        const r7 = resultNumFn(c5, t3, b2);
        const r8 = c6;
        const r9 = resultNumFn(null, t1, b3);
        const r10 = resultNumFn(c7, t2, b3);
        const r11 = resultNumFn(c8, t3, b3);
        const r12 = c9;

        const fR1 = finalResultFn(null, r1, 0, 0);
        const fR2 = finalResultFn(fR1.carryingNum, r2, r5, 0);
        const fR3 = finalResultFn(fR2.carryingNum, r3, r6, r9);
        const fR4 = finalResultFn(fR3.carryingNum, r4, r7, r10);
        const fR5 = finalResultFn(fR4.carryingNum, 0, r8, r11);
        const fR6 = finalResultFn(fR5.carryingNum, 0, 0, r12);

        return {
            ...state,
            answer:{
                ...state.answer,
                number : {
                    t1: t1,
                    t2: t2,
                    t3: t3,
                    b1: b1,
                    b2: b2,
                    b3: b3
                },
                carry : {
                    cL1 : {
                        cL11: c1,
                        cL12: c2,
                        cL13: c3
                    },
                    cL2 : {
                        cL21: c4,
                        cL22: c5,
                        cL23: c6
                    },
                    cL3 : {
                        cL31: c7,
                        cL32: c8,
                        cL33: c9
                    }
                },
                result : {
                    rL1 : {
                        rL11: r1,
                        rL12: r2,
                        rL13: r3,
                        rL14: r4,
                    },
                    rL2 : {
                        rL21: r5,
                        rL22: r6,
                        rL23: r7,
                        rL24: r8,
                    },
                    rL3 : {
                        rL31: r9,
                        rL32: r10,
                        rL33: r11,
                        rL34: r12,
                    }
                },
                finalResult:{
                    r:{
                        fr1: fR1.resultNum,
                        fr2: fR2.resultNum,
                        fr3: fR3.resultNum,
                        fr4: fR4.resultNum,
                        fr5: fR5.resultNum,
                        fr6: fR6.resultNum,
                    },
                    c:{
                        fc1: fR1.carryingNum,
                        fc2: fR2.carryingNum,
                        fc3: fR3.carryingNum,
                        fc4: fR4.carryingNum,
                        fc5: fR5.carryingNum
                    }
                }
            }
        }
    }else{
        return state
    }
};

export default multiplication;